import React, { Component } from 'react';
import moment from 'moment';
import { requestHeaderDELETEWithAuth, requestHeaderGETWithAuth, requestHeaderPOSTWithAuth } from 'src/Rest-API/Header';
import { getUserDataFromLocalStorage, logoutProcedureForLocalStorage } from 'src/Utils/AuthRelatedThings';
import { Logger, getSearchResult, openPageInNewTab, reloadPage, goHome } from '../../Utils/Utils';
import { Loading } from '../Statuses/Statuses';
import Table from 'react-bootstrap/Table';
import {
    CButton,
    CCard,
    CCardBody,
    CCardGroup,
    CCol,
    CContainer,
    CForm,
    CInput,
    CInputGroup,
    CInputGroupPrepend,
    CInputGroupText,
    CRow,
    CTextarea
  } from '@coreui/react';
import CIcon from '@coreui/icons-react';
import StatusModal from '../Modals/StatusModal';
import FailureModal from '../Modals/FailureModal';
import { brandSet } from '@coreui/icons';
import MaterialReactTable from 'material-react-table';
import { BACKEND_PRICE_TRACKER_SERVICE_BASE_URL } from 'src/Rest-API/EnvironmentSpecificThings';

//default
const FILE_NAME = "Location.js";

//default
const initialState = {
    isLoaded: false,
    loadingModalVisible: false,
    loadingModalState: 0,
    failureModalVisible: false,
    locationListData: []
};

const tableColumnData = [
    {
        accessorKey: 'id',
        header: 'ID',
        enableEditing: false,
    },
    {
        accessorKey: 'name',
        header: 'Name',
        enableEditing: true,
    },
    {
        accessorKey: 'description',
        header: 'Description',
        enableEditing: true,
    },
    {
        accessorKey: 'latLong',
        header: 'Lat-Long',
        enableEditing: true,
    },
    {
        accessorKey: 'googlePlusCode',
        header: 'Google Plus Code',
        enableEditing: true,
    },
    {
        accessorKey: 'city',
        header: 'city',
        enableEditing: true,
    },
    {
        accessorKey: 'state',
        header: 'state',
        enableEditing: true,
    },
    {
        accessorKey: 'country',
        header: 'country',
        enableEditing: true,
    },
    
    {
        accessorKey: 'createdAt',
        header: 'Created At',
        enableEditing: false,
        Cell: ({ renderedCellValue, row }) => (moment(renderedCellValue).zone('IST').format('YYYY/MM/DD HH:mm:ss ddd'))
    },
    {
        accessorKey: 'updatedAt',
        header: 'Updated At',
        enableEditing: false,
        Cell: ({ renderedCellValue, row }) => (moment(renderedCellValue).zone('IST').format('YYYY/MM/DD HH:mm:ss ddd'))
    },
];


class Location extends Component {

    //default
    constructor(props) {
        super(props);
        this.state = JSON.parse(JSON.stringify(initialState));
    }

    //default
    resetStateToDefault() {
        this.setState(JSON.parse(JSON.stringify(initialState)));
    }

    //default
    reloadData() {
        this.resetStateToDefault();
        this.fetchLocationDataList();
    }

    //default
    componentDidMount() {
        document.title = "Satanlabs - expenses - Location page";
        this.fetchLocationDataList();
    }

    fetchLocationDataList() {
        let API_URL = BACKEND_PRICE_TRACKER_SERVICE_BASE_URL + '/location/list?offset=0&from=admin-portal';
        const userData = getUserDataFromLocalStorage();

        fetch(API_URL, requestHeaderGETWithAuth(userData.authToken))
        .then(response => {
            if(response.status === 200)
                return response.json();
            if(response.status === 401) {
                this.setState({
                    loadingModalVisible: false,
                    loadingModalState: 0,
                    failureModalVisible: true
                });
                setTimeout(() => {
                    logoutProcedureForLocalStorage();
                    reloadPage();
                }, 500);
            } else {
                this.setState({
                    loadingModalVisible: false,
                    loadingModalState: 0,
                    failureModalVisible: true
                });
            }
            throw new Error("HTTP status is " + response.status);
        })
        .then(
            (result) => {
                Logger(FILE_NAME, "fetchLocationDataList", "FETCH_1", "result", result);
                if(result.code === 1001) {
                    //success
                    this.setState({
                        isLoaded: true,
                        locationListData: result.data
                    });
                } else {
                    //something horrible happened
                    this.setState({
                        loadingModalVisible: false,
                        loadingModalState: 0,
                        failureModalVisible: true
                    });
                }
            },
            (error) => {
                Logger(FILE_NAME, "fetchLocationDataList", "FETCH_1", "error", error);
                this.setState({
                    loadingModalVisible: false,
                    loadingModalState: 0,
                    failureModalVisible: true
                });
            }
        );
    }

    submitCreateLocationForm = (event) => {
        event.preventDefault();
        
        let reqData = {
            name: event.target.inputName.value,
            description: event.target.inputDescription.value,
            latLong: event.target.inputLatLong.value,
            googlePlusCode: event.target.inputGooglePlusCode.value,
            city: event.target.inputCity.value,
            state: event.target.inputState.value,
            country: event.target.inputCountry.value
        };

        this.setState({
            loadingModalVisible: true,
            loadingModalState: 0
        });

        this.callCreateLocationApi(reqData);
    }

    //todo: - arsh - https://igod.gov.in/sg/district/states
    callCreateLocationApi(newLocation) {
        let API_URL = BACKEND_PRICE_TRACKER_SERVICE_BASE_URL + '/location/save?offset=0&from=admin-portal';
        const userData = getUserDataFromLocalStorage();
        fetch(API_URL, requestHeaderPOSTWithAuth(JSON.stringify(newLocation), userData.authToken))
        .then(response => {
            if(response.status === 200)
                return response.json();
            if(response.status === 401) {
                this.setState({
                    loadingModalVisible: false,
                    loadingModalState: 0,
                    failureModalVisible: true
                });
                setTimeout(() => {
                    logoutProcedureForLocalStorage();
                    reloadPage();
                }, 500);
            } else {
                this.setState({
                    loadingModalVisible: false,
                    loadingModalState: 0,
                    failureModalVisible: true
                });
            }
            throw new Error("HTTP status is " + response.status);
        })
        .then(
            (result) => {
                Logger(FILE_NAME, "callCreateLocationApi", "FETCH_1", "result", result);
                if(result.code === 1001) {
                    //success
                    this.setState({
                        loadingModalState: 1,
                    });
                    setTimeout(() => {this.reloadData();}, 200);
                } else {
                    //something horrible happened
                    this.setState({
                        loadingModalVisible: false,
                        loadingModalState: 0,
                        failureModalVisible: true
                    });
                }
            },
            (error) => {
                Logger(FILE_NAME, "callCreateLocationApi", "FETCH_1", "error", error);
                this.setState({
                    loadingModalVisible: false,
                    loadingModalState: 0,
                    failureModalVisible: true
                });
            }
        );
    }

    //default
    render() {
        //default
        Logger(FILE_NAME, "render", "", "this.state", this.state);

        return (
            this.state.isLoaded ? 
            <div>
                <h1>LOCATION &nbsp;&nbsp;&nbsp;&nbsp;&nbsp; 
                    <CButton color="success" className="px-4" onClick={() => this.reloadData()}>Reload Data</CButton>
                </h1>
                <br />

                {
                        /* Create new location */
                        <CCol md="5">
                            <CCardGroup>
                            <CCard className="p-4">
                                <CCardBody>
                                <CForm onSubmit={this.submitCreateLocationForm}>
                                    <h1>Save a new Location</h1>
                                    <CInputGroup className="mb-3">
                                        <CInputGroupPrepend>
                                            <CInputGroupText>
                                            <CIcon name="cilPencil" />
                                            </CInputGroupText>
                                        </CInputGroupPrepend>
                                        <CInput
                                            placeholder="Location Name" 
                                            autoComplete="on"
                                            type="text"
                                            step="any"
                                            id="inputName"
                                            label="Location Name"
                                            required
                                        />
                                    </CInputGroup>

                                    <CInputGroup className="mb-3">
                                        <CInputGroupPrepend>
                                            <CInputGroupText>
                                            <CIcon name="cilPencil" />
                                            </CInputGroupText>
                                        </CInputGroupPrepend>
                                        <CInput
                                            placeholder="Location Description" 
                                            autoComplete="on"
                                            type="text"
                                            step="any"
                                            id="inputDescription"
                                            label="Location Description"
                                            required
                                        />
                                    </CInputGroup>

                                    <CInputGroup className="mb-3">
                                        <CInputGroupPrepend>
                                            <CInputGroupText>
                                            <CIcon name="cilPencil" />
                                            </CInputGroupText>
                                        </CInputGroupPrepend>
                                        <CInput
                                            placeholder="LatLong" 
                                            autoComplete="on"
                                            type="text"
                                            step="any"
                                            id="inputLatLong"
                                            label="LatLong"
                                            required
                                        />
                                    </CInputGroup>

                                    <CInputGroup className="mb-3">
                                        <CInputGroupPrepend>
                                            <CInputGroupText>
                                            <CIcon name="cilPencil" />
                                            </CInputGroupText>
                                        </CInputGroupPrepend>
                                        <CInput
                                            placeholder="Google Plus Code" 
                                            autoComplete="on"
                                            type="text"
                                            step="any"
                                            id="inputGooglePlusCode"
                                            label="Google Plus Code"
                                            required
                                        />
                                    </CInputGroup>

                                    <CInputGroup className="mb-3">
                                        <CInputGroupPrepend>
                                            <CInputGroupText>
                                            <CIcon name="cilPencil" />
                                            </CInputGroupText>
                                        </CInputGroupPrepend>
                                        <CInput
                                            placeholder="city" 
                                            autoComplete="on"
                                            type="text"
                                            step="any"
                                            id="inputCity"
                                            label="city"
                                            required
                                        />
                                    </CInputGroup>

                                    <CInputGroup className="mb-3">
                                        <CInputGroupPrepend>
                                            <CInputGroupText>
                                            <CIcon name="cilPencil" />
                                            </CInputGroupText>
                                        </CInputGroupPrepend>
                                        <CInput
                                            placeholder="state" 
                                            autoComplete="on"
                                            type="text"
                                            step="any"
                                            id="inputState"
                                            label="state"
                                            required
                                        />
                                    </CInputGroup>

                                    <CInputGroup className="mb-3">
                                        <CInputGroupPrepend>
                                            <CInputGroupText>
                                            <CIcon name="cilPencil" />
                                            </CInputGroupText>
                                        </CInputGroupPrepend>
                                        <CInput
                                            placeholder="country" 
                                            autoComplete="on"
                                            type="text"
                                            step="any"
                                            id="inputCountry"
                                            label="country"
                                            required
                                        />
                                    </CInputGroup>

                                    <CRow>
                                        <CCol xs="6">
                                            <CButton color="success" className="px-4" type='submit'>SAVE</CButton>
                                        </CCol>
                                        <CCol xs="6" className="text-right">
                                            <CButton color="danger" className="px-0" onClick={() => this.reloadData()}>&nbsp;Reset values&nbsp;</CButton>
                                        </CCol>
                                    </CRow>
                                </CForm>
                                </CCardBody>
                            </CCard>
                            </CCardGroup>
                        </CCol>
                            
                    }
                    <br /><br />

                    {
                    this.state.isLoaded ?
                        <MaterialReactTable
                            initialState={{ density: 'compact' }}
                            columns={tableColumnData} 
                            data={this.state.locationListData}
                            enablePagination = {true}

                            editingMode={"row"}
                            enableEditing
                            onEditingRowSave={(exitEditingMode) => this.handleSaveRowEdits(exitEditingMode)}
                        /> : <br/>
                    }

                {/* //default */}
                <StatusModal visible={this.state.loadingModalVisible} loadingModalState={this.state.loadingModalState} />
                <FailureModal visible={this.state.failureModalVisible} />
            </div>
            : <Loading />
        );
    }
}

export default Location;
